.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.header {
  background-color: var(--bg-secondary);
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 20px;
  margin: 1rem auto;
  width: 90%;
  max-width: 90%;
  position: sticky;
  top: 1rem;
  z-index: 10;
  backdrop-filter: var(--blur);
  -webkit-backdrop-filter: var(--blur);
  border: 1px solid var(--border);
  box-shadow: var(--shadow);
}

.header h1 {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header h1 a {
  color: var(--text-primary);
  display: flex;
  align-items: center;
}

.header h1 a:hover {
  color: var(--text-secondary);
  transition: color 0.2s ease;
}

.header h1 img {
  width: 40px;
  margin-right: 0.5rem;
  vertical-align: middle;
  object-fit: contain;
  flex-shrink: 0;
}

.nav {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 0.5rem;
}

.nav li a {
  color: var(--text-primary);
  padding: 0.5rem 0.8rem;
  border-radius: 16px;
  font-size: 0.9rem;
}

.nav li a:hover {
  color: var(--text-secondary);
  background-color: rgba(0, 0, 0, 0.05);
  transition: color 0.2s ease, background-color 0.2s ease;
}

.main {
  flex: 1;
  padding: 1rem;
  max-width: 1000px;
  margin: 0 auto;
  width: 100%;
  position: relative;
  z-index: 1;
}

.hero {
  text-align: center;
  padding: 3rem 0;
}

.hero h1 {
  font-size: clamp(1.8rem, 4vw, 3rem);
  margin-bottom: 0.8rem;
  background: linear-gradient(135deg, var(--accent), var(--accent-hover));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 600;
}

.hero p {
  font-size: 1rem;
  margin-bottom: 1.5rem;
  opacity: 0.9;
}

.cta-button {
  display: inline-block;
  background: linear-gradient(135deg, var(--accent), var(--accent-hover));
  color: var(--text-primary);
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  font-weight: 500;
  box-shadow: var(--shadow);
}

.cta-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(202, 138, 98, 0.2);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.content {
  line-height: 1.7;
  font-size: 1rem;
}

.content h1, .content h2, .content h3 {
  margin-top: 1.5rem;
  color: var(--text-primary);
  font-weight: 500;
}

.content ul, .content ol {
  padding-left: 1.2rem;
}

.content li {
  margin-bottom: 0.4rem;
}

.theme-toggle {
  font-size: 1.2rem;
  padding: 0.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.theme-toggle .material-icons-round {
  font-size: 1.5rem;
  color: var(--text-primary);
}

.menu-toggle {
  font-size: 1.2rem;
  padding: 0.4rem;
  display: none;
  align-items: center;
  justify-content: center;
}

.menu-toggle .material-icons-round {
  font-size: 1.5rem;
  color: var(--text-primary);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: var(--blur);
  -webkit-backdrop-filter: var(--blur);
  z-index: 8;
}

.footer {
  text-align: center;
  padding: 1rem 1rem;
  border-top: 1px solid var(--border);
  margin-top: 2rem;
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.footer a {
  color: var(--accent);
}

.footer a:hover {
  color: var(--accent-hover);
}

.GuideImage {
  max-width: 50%;
  margin: 1rem 0;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@media (max-width: 768px) {
  .header {
    padding: 0.4rem 0.8rem;
    margin: 0.5rem auto;
    justify-content: space-between;
    gap: 0.5rem;
  }

  .header h1 {
    order: 2;
    flex-grow: 1;
    text-align: center;
  }

  .menu-toggle {
    display: flex;
    order: 1;
  }

  .theme-toggle {
    order: 3;
  }

  .nav {
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: calc(100% + 0.5rem);
    left: 0;
    right: 0;
    background-color: var(--bg-secondary);
    backdrop-filter: var(--blur);
    -webkit-backdrop-filter: var(--blur);
    border: 1px solid var(--border);
    border-radius: 20px;
    box-shadow: var(--shadow);
    z-index: 9;
    transform: none;
  }

  .nav.open {
    opacity: 1;
    pointer-events: auto;
  }

  .nav ul {
    flex-direction: column;
    margin: 0;
    padding: 1rem;
  }

  .nav li a {
    display: block;
    padding: 0.8rem;
    border-radius: 12px;
  }

  .main {
    padding: 0.8rem;
  }

  .hero {
    padding: 1.5rem 0;
  }

  .content {
    font-size: 0.9rem;
  }

  .footer {
    padding: 1.5rem 0.8rem;
  }

  .GuideImage {
    max-width: 100%;
  }
}
